import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { RX_OVERLAY } from './overlay.constant';

@Pipe({
  name: 'rxCustomizationStatus'
})
export class RxCustomizationStatusPipe implements PipeTransform {
  constructor(private translateService: TranslateService) {}

  transform(value: string): string {
    switch (value) {
      case RX_OVERLAY.customizationPerspective.createdInThisOverlayGroup:
        return this.translateService.instant(
          'com.bmc.arsys.rx.innovation-studio.customization-status.created-in-this-overlay-group.label'
        );

      case RX_OVERLAY.customizationPerspective.customizedInThisOverlayGroup:
        return this.translateService.instant(
          'com.bmc.arsys.rx.innovation-studio.customization-status.customized-in-this-overlay-group.label'
        );

      case RX_OVERLAY.customizationPerspective.notCustomizedInThisOverlayGroup:
        return this.translateService.instant(
          'com.bmc.arsys.rx.innovation-studio.customization-status.not-customized-in-this-overlay-group.label'
        );

      default:
        return '';
    }
  }
}
